import {
  fillAll, getPixelColor,
} from '../utils/utils';

const fillAllPixels = (context, point, canvasState, changeCanvasState, newColor) => {
  const { size } = canvasState;
  const frameState = context.getImageData(0, 0, size, size).data;
  const selectedColor = getPixelColor(frameState, point, canvasState.size);
  if (selectedColor === newColor) return;
  const newFrameState = fillAll(frameState, selectedColor, newColor);
  const newImageData = new ImageData(newFrameState, size, size);
  context.putImageData(newImageData, 0, 0, 0, 0, size, size);
  changeCanvasState({ canvasState: newImageData });
};

export default fillAllPixels;
