import {
  SET_CURRENT_TOOL,
  SET_MAIN_COLOR,
  SET_SECOND_COLOR,
  SWAP_COLORS,
  SET_PEN_SIZE,
  SET_CANVAS_STATE,
  ADD_FRAME,
  SET_CURRENT_FRAME,
  DELETE_FRAME,
  COPY_FRAME,
  SET_FRAME_RATE,
  TOGGLE_PREVIEW_FULLSCREEN,
  SET_SIZE,
  SWAP_FRAME,
} from './actionsTypes';

export const setCurrentTool = (tool) => (
  {
    type: SET_CURRENT_TOOL,
    payload: tool,
  }
);

export const setMainColor = (color) => (
  {
    type: SET_MAIN_COLOR,
    payload: color,
  }
);

export const setSecondColor = (color) => (
  {
    type: SET_SECOND_COLOR,
    payload: color,
  }
);

export const swapColors = () => (
  {
    type: SWAP_COLORS,
  }
);

export const setPenSize = (penSize) => (
  {
    type: SET_PEN_SIZE,
    payload: penSize,
  }
);

export const setCanvasState = (props) => (
  {
    type: SET_CANVAS_STATE,
    payload: props,
  }
);

export const addFrame = (frame) => (
  {
    type: ADD_FRAME,
    payload: frame,
  }
);

export const setCurrentFrame = (index) => (
  {
    type: SET_CURRENT_FRAME,
    payload: index,
  }
);

export const deleteFrame = (index) => (
  {
    type: DELETE_FRAME,
    payload: index,
  }
);

export const copyFrame = (index) => (
  {
    type: COPY_FRAME,
    payload: index,
  }
);

export const setFrameRate = (frameRate) => (
  {
    type: SET_FRAME_RATE,
    payload: frameRate,
  }
);

export const togglePreviewFullscreen = (isOpen) => (
  {
    type: TOGGLE_PREVIEW_FULLSCREEN,
    payload: isOpen,
  }
);

export const setSize = (size) => (
  {
    type: SET_SIZE,
    payload: size,
  }
);

export const swapFrame = (index1, index2) => (
  {
    type: SWAP_FRAME,
    payload: { index1, index2 },
  }
);
