import {
  isEqualPoint, drawPixels, getPointPosition, getLinePoints,
} from '../utils/utils';

const drawStrokeLine = (
  mainContext, drawingContext, startPoint, canvasPosition, canvasState, setCanvasState, penSize,
) => {
  const { pixelSize, size } = canvasState;
  let point = startPoint;
  let prevPoint = point;
  const pointList = [point];
  drawPixels(drawingContext, pointList, penSize);
  let linePoints = [];
  const onMouseMove = ({ pageX, pageY }) => {
    point = getPointPosition(pageX, pageY, pixelSize, canvasPosition);

    if (!isEqualPoint(point, prevPoint)) {
      drawingContext.clearRect(0, 0, size, size);
      linePoints = getLinePoints(startPoint, point);
      drawPixels(drawingContext, linePoints, penSize);
      prevPoint = point;
    }
  };

  const onMouseUp = () => {
    drawingContext.clearRect(0, 0, size, size);
    drawPixels(mainContext, linePoints, penSize);
    setCanvasState({ canvasState: mainContext.getImageData(0, 0, size, size) });
    document.removeEventListener('mousemove', onMouseMove);
    document.removeEventListener('mouseup', onMouseUp);
  };
  document.addEventListener('mousemove', onMouseMove);
  document.addEventListener('mouseup', onMouseUp);
};

export default drawStrokeLine;
