import {
  SET_CURRENT_TOOL,
  SET_MAIN_COLOR,
  SET_SECOND_COLOR,
  SWAP_COLORS,
  SET_PEN_SIZE,
  SET_CANVAS_STATE,
  ADD_FRAME,
  SET_CURRENT_FRAME,
  DELETE_FRAME,
  COPY_FRAME,
  SET_FRAME_RATE,
  TOGGLE_PREVIEW_FULLSCREEN,
  SET_SIZE,
  SWAP_FRAME,
} from './actionsTypes';

import { createInitialFrameState, copyFrame } from '../utils/utils';

const reducer = (state, action) => {
  switch (action.type) {
    case SET_CURRENT_TOOL:
      return { ...state, currentTool: action.payload };
    case SET_PEN_SIZE:
      return { ...state, penSize: action.payload };
    case SET_MAIN_COLOR:
      return { ...state, mainColor: action.payload };
    case SET_SECOND_COLOR:
      return { ...state, secondColor: action.payload };
    case SWAP_COLORS: {
      const { mainColor, secondColor } = state;
      return {
        ...state,
        mainColor: secondColor,
        secondColor: mainColor,
      };
    }
    case SET_CANVAS_STATE: {
      const { canvasState } = action.payload;
      const { frames, currentFrame: index } = state;
      const newState = {
        ...state,
        frames: [...frames.slice(0, index), canvasState, ...frames.slice(index + 1)],
      };
      return newState;
    }
    case ADD_FRAME: {
      const { frames, currentFrame: index } = state;
      return {
        ...state,
        frames: [...frames.slice(0, index + 1), action.payload, ...frames.slice(index + 1)],
        currentFrame: state.currentFrame + 1,
      };
    }
    case SET_CURRENT_FRAME:
      return { ...state, currentFrame: action.payload };
    case DELETE_FRAME: {
      const { frames, currentFrame } = state;
      const index = action.payload;
      const isChangeCurrentFrame = index === frames.length - 1
        || currentFrame === frames.length - 1;
      const newCurrentFrame = isChangeCurrentFrame ? currentFrame - 1 : currentFrame;
      return {
        ...state,
        frames: [...frames.slice(0, index), ...frames.slice(index + 1)],
        currentFrame: newCurrentFrame,
      };
    }
    case COPY_FRAME: {
      const { frames, currentFrame } = state;
      const index = action.payload;
      const newFrame = copyFrame(frames[index]);
      return {
        ...state,
        frames: [...frames.slice(0, index + 1), newFrame, ...frames.slice(index + 1)],
        currentFrame: currentFrame + 1,
      };
    }
    case SET_FRAME_RATE:
      return { ...state, frameRate: action.payload };
    case TOGGLE_PREVIEW_FULLSCREEN:
      return { ...state, isOpenFullscreen: action.payload };
    case SET_SIZE: {
      const { canvas } = state;
      const newFrameState = createInitialFrameState(action.payload);
      const pixelSize = canvas.width / action.payload;
      const newCanvas = { ...canvas, pixelSize, size: action.payload };
      return {
        ...state, canvas: newCanvas, frames: [newFrameState], currentFrame: 0,
      };
    }
    case SWAP_FRAME: {
      const { frames } = state;
      const { index1, index2 } = action.payload;
      const frame1 = frames[index1];
      const frame2 = frames[index2];
      const newFrames = frames.slice();
      newFrames[index1] = frame2;
      newFrames[index2] = frame1;
      return { ...state, frames: newFrames };
    }
    default:
      return state;
  }
};

export default reducer;
