import UPNG from 'upng-js';
import saveFile from 'save-file';

import { clearCanvas, drawFrame } from '../utils/utils';

const getArrayBuffers = (layer, size) => {
  const canvas = document.createElement('canvas');
  canvas.height = size;
  canvas.width = size;
  const context = canvas.getContext('2d');
  return layer.map((frame) => {
    clearCanvas(context, size);
    drawFrame(context, frame, size);
    return context.getImageData(0, 0, size, size).data.buffer;
  });
};

const getIntervals = (layer, interval) => layer.map(() => interval * 1000);

const saveAsApng = (layer, size, interval = 300, fileName = 'animation') => {
  const arrayBuffers = getArrayBuffers(layer, size);
  const intervals = getIntervals(layer, interval);
  const arr = UPNG.encode(arrayBuffers, size, size, 0, intervals);
  saveFile(arr, `${fileName}.apng`);
};

export default saveAsApng;
