export const rgbaToHex = (rgba) => {
  const componentToHex = (component) => {
    const hex = component.toString(16);
    return hex.length === 1 ? `0${hex}` : hex;
  };
  const rgb = rgba.slice(0, 3);
  return rgb.reduce((acc, item) => `${acc}${componentToHex(item)}`, '#');
};

export const hexToRgba = (hex) => {
  const result = /^#?([a-f\d]{2})([a-f\d]{2})([a-f\d]{2})([a-f\d]{2})?$/i.exec(hex);
  return result ? [
    parseInt(result[1], 16),
    parseInt(result[2], 16),
    parseInt(result[3], 16),
    result[4] ? parseInt(result[4], 16) : 255,
  ] : null;
};

export const getPointPosition = (screenX, screenY, pixelSize, canvasPosition) => {
  const x = Math.floor((screenX - canvasPosition.x) / pixelSize);
  const y = Math.floor((screenY - canvasPosition.y) / pixelSize);
  return { x, y };
};

export const isEqualPoint = (point1, point2) => point1.x === point2.x && point1.y === point2.y;

export const getLinePoints = (point1, point2) => {
  const { x: x1, y: y1 } = point1; const { x: x2, y: y2 } = point2;
  let x; let y; let px; let py; let xe; let ye; let i;
  const filledPixels = [];
  const dx = x2 - x1;
  const dy = y2 - y1;
  const dx1 = Math.abs(dx);
  const dy1 = Math.abs(dy);

  px = 2 * dy1 - dx1;
  py = 2 * dx1 - dy1;

  if (dy1 <= dx1) {
    if (dx >= 0) {
      x = x1; y = y1; xe = x2;
    } else {
      x = x2; y = y2; xe = x1;
    }
    filledPixels.push({ x, y });
    for (i = 0; x < xe; i += 1) {
      x += 1;
      if (px < 0) {
        px += 2 * dy1;
      } else {
        if ((dx < 0 && dy < 0) || (dx > 0 && dy > 0)) {
          y += 1;
        } else {
          y -= 1;
        }
        px += 2 * (dy1 - dx1);
      }
      filledPixels.push({ x, y });
    }
  } else {
    if (dy >= 0) {
      x = x1; y = y1; ye = y2;
    } else {
      x = x2; y = y2; ye = y1;
    }
    filledPixels.push({ x, y });
    for (i = 0; y < ye; i += 1) {
      y += 1;
      if (py <= 0) {
        py += 2 * dx1;
      } else {
        if ((dx < 0 && dy < 0) || (dx > 0 && dy > 0)) {
          x += 1;
        } else {
          x -= 1;
        }
        py += 2 * (dx1 - dy1);
      }
      filledPixels.push({ x, y });
    }
  }
  return filledPixels;
};

export const drawPixels = (context, pointList, pixelSize = 1) => {
  pointList.forEach((point) => {
    const { x, y } = point;
    context.fillRect(x, y, pixelSize, pixelSize);
  });
};

export const clearPixels = (context, pointList, pixelSize = 1) => {
  pointList.forEach((point) => {
    const { x, y } = point;
    context.clearRect(x, y, pixelSize, pixelSize);
  });
};

export const getPixelColor = (state, point, canvasSize) => {
  const { x, y } = point;
  const startIndex = (y * canvasSize + x) * 4;
  return state.slice(startIndex, startIndex + 4);
};

export const fillArea = (canvasState, size, point, startColor, color) => {
  const state = canvasState.slice();
  const [r, g, b] = color;
  const pixelStack = [[point.y, point.x]];
  let newPos;

  const matchColors = (position) => {
    const r = state[position];
    const g = state[position + 1];
    const b = state[position + 2];
    return r === startColor[0] && g === startColor[1] && b === startColor[2];
  };

  const colorPixel = (position) => {
    state[position] = r;
    state[position + 1] = g;
    state[position + 2] = b;
    state[position + 3] = 255;
  };

  while (pixelStack.length) {
    let i; let j; let reachLeft; let reachRight; let position;
    newPos = pixelStack.pop();
    [i, j] = newPos;
    position = (i * size + j) * 4;
    while (i >= 0 && matchColors(position)) {
      position -= size * 4;
      i -= 1;
    }
    position += size * 4;
    i += 1;
    reachLeft = false;
    reachRight = false;

    while (i < size && matchColors(position)) {
      colorPixel(position);
      if (j > 0) {
        if (matchColors(position - 4)) {
          if (!reachLeft) {
            pixelStack.push([i, j - 1]);
            reachLeft = true;
          }
        } else if (reachLeft) {
          reachLeft = false;
        }
      }
      if (j < size - 1) {
        if (matchColors(position + 4)) {
          if (!reachRight) {
            pixelStack.push([i, j + 1]);
            reachRight = true;
          }
        } else if (reachRight) {
          reachRight = false;
        }
      }
      i += 1;
      position += size * 4;
    }
  }
  return state;
};

export const fillAll = (canvasState, color, newColor) => {
  const state = canvasState.slice();
  for (let i = 0; i < state.length; i += 4) {
    const isEqualColors = state[i] === color[0] && state[i + 1] === color[1]
      && state[i + 2] === color[2] && state[i + 3] === color[3];
    if (isEqualColors) {
      const [r, g, b, opacity] = newColor;
      state[i] = r;
      state[i + 1] = g;
      state[i + 2] = b;
      state[i + 3] = opacity;
    }
  }
  return state;
};

export const createInitialFrameState = (size) => {
  const canvas = document.createElement('canvas');
  canvas.width = size;
  canvas.height = size;
  const context = canvas.getContext('2d');
  const X_STARTING_POINT = 0;
  const Y_STARTING_POINT = 0;
  return context.getImageData(X_STARTING_POINT, Y_STARTING_POINT, size, size);
};

export const copyFrame = (frame) => {
  const { width, height, data } = frame;
  const newData = data.slice();
  return new ImageData(newData, width, height);
};

export const createInterval = (fn) => {
  let intervalId = null;
  return {
    clear: () => {
      if (intervalId) {
        clearInterval(intervalId);
      }
    },
    start: (time) => {
      intervalId = setInterval(fn, time);
    },
  };
};
export const clearCanvas = (context, size) => {
  const X_STARTING_POINT = 0;
  const Y_STARTING_POINT = 0;
  context.clearRect(X_STARTING_POINT, Y_STARTING_POINT, size, size);
};

export const drawFrame = (context, imageData, size) => {
  context.putImageData(imageData, 0, 0, 0, 0, size, size);
};

export const getFrames = (list) => list.map((item) => {
  const { width, height, data } = item;
  const uint8ClampedArray = Uint8ClampedArray.from(data);
  return new ImageData(uint8ClampedArray, width, height);
});

export const convertImageDataToObject = (list) => list.map((item) => {
  const { width, height, data } = item;
  const array = Array.from(data);
  return { width, height, data: array };
});
