import React from 'react';
import { connect } from 'react-redux';

import { convertImageDataToObject } from '../../utils/utils';

import './SaveMenu.scss';


const SaveMenu = (props) => {
  const handleClickSave = () => {
    // eslint-disable-next-line react/prop-types
    const { frames } = props;
    const appState = JSON.stringify({ ...props, frames: convertImageDataToObject(frames) });
    localStorage.setItem('piskelclone', appState);
  };

  const handleClickClear = () => {
    localStorage.removeItem('piskelclone');
  };

  return (
    <div className="save-menu">
      <button className="save-menu__button" type="button" onClick={handleClickSave}>Save</button>
      <button className="save-menu__button" type="button" onClick={handleClickClear}>Delete save</button>
    </div>
  );
};

const mapStateToProps = (state) => (state);

export default connect(mapStateToProps)(SaveMenu);
