import React from 'react';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';

import Button from '../Button';
import { setSize } from '../../redux/actions';

import './ResizeMenu.scss';

const CANVAS_RESOLUTION = {
  LOW: 32,
  NORMAL: 64,
  HIGH: 128,
};

const ResizeMenu = ({ size, setSize: resize }) => (
  <div className="resize-menu">
    <Button
      isActive={size === CANVAS_RESOLUTION.LOW}
      handleClick={() => resize(CANVAS_RESOLUTION.LOW)}
    >
      32
    </Button>
    <Button
      isActive={size === CANVAS_RESOLUTION.NORMAL}
      handleClick={() => resize(CANVAS_RESOLUTION.NORMAL)}
    >
      64
    </Button>
    <Button
      isActive={size === CANVAS_RESOLUTION.HIGH}
      handleClick={() => resize(CANVAS_RESOLUTION.HIGH)}
    >
      128
    </Button>
  </div>
);

const mapStateToProps = ({ canvas: { size } }) => ({ size });

const mapDispatchToProps = {
  setSize,
};

ResizeMenu.propTypes = {
  size: PropTypes.number.isRequired,
  setSize: PropTypes.func.isRequired,
};

export default connect(mapStateToProps, mapDispatchToProps)(ResizeMenu);
