import React, { Component } from 'react';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';

import {
  getPointPosition, rgbaToHex, drawFrame, clearCanvas,
} from '../../utils/utils';
import { setCanvasState, setMainColor } from '../../redux/actions';
import drawLine from '../../tools/drawLine';
import drawStrokeLine from '../../tools/drawStrokeLine';
import eraseLine from '../../tools/eraseLine';
import getColor from '../../tools/getColor';
import fillAllPixels from '../../tools/fillAllPixels';
import fill from '../../tools/fill';

import './Canvas.scss';

class Canvas extends Component {
  constructor() {
    super();
    this.mainCanvasRef = React.createRef();
    this.drawingCanvasRef = React.createRef();
    this.mainCanvas = null;
    this.drawingCanvas = null;
    this.mainContext = null;
    this.drawingContext = null;
    this.screen = null;
    this.handleMouseDown = this.handleMouseDown.bind(this);
  }

  componentDidMount() {
    const { currentFrame, frames, canvas: { size } } = this.props;
    this.mainCanvas = this.mainCanvasRef.current;
    this.mainContext = this.mainCanvas.getContext('2d');
    this.drawingCanvas = this.drawingCanvasRef.current;
    this.drawingContext = this.drawingCanvas.getContext('2d');
    drawFrame(this.mainContext, frames[currentFrame], size);
  }

  shouldComponentUpdate(nextProps) {
    const { currentFrame, frames, canvas } = this.props;
    const { currentFrame: nextCurrentFrame, frames: nextFrames, canvas: newCanvas } = nextProps;
    if (currentFrame !== nextCurrentFrame || frames.length !== nextFrames.length
      || canvas !== newCanvas) {
      return true;
    }
    return false;
  }

  componentDidUpdate() {
    const { frames, currentFrame, canvas } = this.props;
    const { size } = canvas;
    const frame = frames[currentFrame];
    clearCanvas(this.mainContext, size);
    if (frame) {
      drawFrame(this.mainContext, frame, size);
    }
  }

  handleMouseDown(event) {
    const {
      mainColor, currentTool, canvas, setCanvasState: changeCanvasState,
      penSize, setMainColor: changeMainColor, secondColor,
    } = this.props;
    event.preventDefault();
    if (currentTool === '') return;
    const { pageX, pageY } = event;
    const canvasPosition = this.mainCanvas.getBoundingClientRect();
    const MOUSE_KEYS = {
      LEFT: 0,
      RIGHT: 2,
    };
    const color = MOUSE_KEYS.RIGHT === event.button ? secondColor : mainColor;
    this.mainContext.fillStyle = rgbaToHex(color);
    this.drawingContext.fillStyle = rgbaToHex(color);
    const point = getPointPosition(pageX, pageY, canvas.pixelSize, canvasPosition);
    switch (currentTool) {
      case 'pen':
        drawLine(this.mainContext, point, canvasPosition, canvas, changeCanvasState, penSize);
        break;
      case 'line':
        drawStrokeLine(
          this.mainContext,
          this.drawingContext,
          point, canvasPosition,
          canvas,
          changeCanvasState,
          penSize,
        );
        break;
      case 'eraser':
        eraseLine(this.mainContext, point, canvasPosition, canvas, changeCanvasState, penSize);
        break;
      case 'pipette':
        getColor(this.mainContext, point, canvas, changeMainColor);
        break;
      case 'fill-all':
        fillAllPixels(this.mainContext, point, canvas, changeCanvasState, color);
        break;
      case 'fill':
        fill(this.mainContext, point, canvas, changeCanvasState, color);
        break;
      default:
        break;
    }
  }

  render() {
    const { canvas: { size } } = this.props;
    return (
      // eslint-disable-next-line jsx-a11y/no-static-element-interactions
      <div
        className="canvas"
        id="screen"
        onMouseDown={this.handleMouseDown}
        ref={this.myRef}
        onContextMenu={(event) => event.preventDefault()}
      >
        <canvas
          className="canvas__main"
          ref={this.mainCanvasRef}
          width={size}
          height={size}
        />
        <canvas
          className="canvas__drawing"
          ref={this.drawingCanvasRef}
          width={size}
          height={size}
        />
        <div className="canvas__background" />
      </div>
    );
  }
}

const mapStateToProps = ({
  mainColor, currentTool, canvas, currentFrame, frames, penSize, secondColor,
}) => ({
  mainColor, currentTool, canvas, currentFrame, frames, penSize, secondColor,
});

const mapDispatchToProps = { setCanvasState, setMainColor };

Canvas.propTypes = {
  mainColor: PropTypes.arrayOf(PropTypes.any).isRequired,
  secondColor: PropTypes.arrayOf(PropTypes.any).isRequired,
  currentTool: PropTypes.string.isRequired,
  currentFrame: PropTypes.number.isRequired,
  frames: PropTypes.arrayOf(PropTypes.any).isRequired,
  canvas: PropTypes.shape({
    width: PropTypes.number.isRequired,
    height: PropTypes.number.isRequired,
    size: PropTypes.number.isRequired,
    pixelSize: PropTypes.number.isRequired,
  }).isRequired,
  setCanvasState: PropTypes.func.isRequired,
  setMainColor: PropTypes.func.isRequired,
  penSize: PropTypes.number.isRequired,
};

export default connect(mapStateToProps, mapDispatchToProps)(Canvas);
