import {
  isEqualPoint, clearPixels, getPointPosition, getLinePoints,
} from '../utils/utils';

const eraseLine = (context, startPoint, canvasPosition, canvasState, setCanvasState, penSize) => {
  const { pixelSize, size } = canvasState;
  let point = startPoint;
  let prevPoint = point;
  const pointList = [point];
  clearPixels(context, pointList, penSize);

  const onMouseMove = ({ pageX, pageY }) => {
    point = getPointPosition(pageX, pageY, pixelSize, canvasPosition);

    if (!isEqualPoint(point, prevPoint)) {
      const linePoints = getLinePoints(prevPoint, point);
      clearPixels(context, linePoints, penSize);
      prevPoint = point;
    }
  };

  const onMouseUp = () => {
    setCanvasState({ canvasState: context.getImageData(0, 0, size, size) });
    document.removeEventListener('mousemove', onMouseMove);
    document.removeEventListener('mouseup', onMouseUp);
  };
  document.addEventListener('mousemove', onMouseMove);
  document.addEventListener('mouseup', onMouseUp);
};

export default eraseLine;
