import React from 'react';
import PropTypes from 'prop-types';

const Tool = (props) => {
  const { tool, clickTool, isActive } = props;
  return (
    <button
      type="button"
      className={`tool tool--${tool.name} ${isActive ? 'tool--active' : ''}`}
      onClick={clickTool}
    >
      {}
    </button>
  );
};

Tool.propTypes = {
  isActive: PropTypes.bool.isRequired,
  tool: PropTypes.shape().isRequired,
  clickTool: PropTypes.func.isRequired,
};

export default Tool;
