import React from 'react';

import ToolBar from '../ToolBar';
import ColorBar from '../ColorBar';
import PenSizePanel from '../PenSizePanel';
import Canvas from '../Canvas';
import Frames from '../Frames';
import Preview from '../Preview';
import PreviewFullscreen from '../PreviewFullscreen';
import ExportMenu from '../ExportMenu';
import ResizeMenu from '../ResizeMenu';
import SaveMenu from '../SaveMenu';

import './Main.scss';

const Main = () => (
  <main className="main">
    <div className="main__left-menu">
      <PenSizePanel />
      <ToolBar />
      <ColorBar />
    </div>
    <div className="main__frames">
      <Frames />
    </div>
    <div className="main__canvas">
      <Canvas />
    </div>
    <div className="main__right-menu">
      <Preview />
      <PreviewFullscreen />
      <ExportMenu />
      <ResizeMenu />
      <SaveMenu />
    </div>
  </main>
);

export default Main;
