import {
  fillArea, getPixelColor,
} from '../utils/utils';

const fill = (context, point, canvasState, changeCanvasState, newColor) => {
  const { size } = canvasState;
  const frameState = context.getImageData(0, 0, size, size).data;
  const startColor = getPixelColor(frameState, point, canvasState.size);
  if (startColor === newColor) return;
  const newFrameState = fillArea(frameState, size, point, startColor, newColor);
  const newImageData = new ImageData(newFrameState, size, size);
  context.putImageData(newImageData, 0, 0, 0, 0, size, size);
  changeCanvasState({ canvasState: newImageData });
};

export default fill;
