import React, { Component } from 'react';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';

import { togglePreviewFullscreen } from '../../redux/actions';
import { createInterval, drawFrame, clearCanvas } from '../../utils/utils';

import './PreviewFullscreen.scss';

class PreviewFullscreen extends Component {
  constructor() {
    super();
    this.canvasRef = React.createRef();
    this.fullScrennRef = React.createRef();
    this.context = null;
    this.showAnimation = this.showAnimation.bind(this);
    this.handleFullScreenChange = this.handleFullScreenChange.bind(this);
    this.previewInterval = createInterval(this.showAnimation);
    this.activeFrame = 0;
  }

  componentDidMount() {
    this.context = this.canvasRef.current.getContext('2d');
    document.addEventListener('fullscreenchange', this.handleFullScreenChange);
  }

  componentDidUpdate() {
    const { isOpenFullscreen } = this.props;
    const MILLISECONDS_PER_SECOND = 1000;
    if (isOpenFullscreen) {
      const fullScreen = this.fullScrennRef.current;
      fullScreen.requestFullscreen();
      const { frameRate } = this.props;
      if (frameRate !== 0) {
        const time = MILLISECONDS_PER_SECOND / frameRate;
        this.previewInterval.start(time);
      } else {
        this.drawFrame();
      }
    }
  }

  handleFullScreenChange() {
    if (!document.fullscreenElement) {
      const { togglePreviewFullscreen: toggleFullscreen } = this.props;
      this.previewInterval.clear();
      toggleFullscreen(false);
    }
  }

  showAnimation() {
    const { frames, canvas: { size } } = this.props;
    const { length } = frames;
    const { activeFrame } = this;
    const frame = frames[activeFrame];
    const context = this.canvasRef.current.getContext('2d');
    clearCanvas(context, size);
    if (frame) {
      drawFrame(context, frame, size);
    }
    this.activeFrame = (this.activeFrame + 1 + length) % length;
  }

  drawFrame() {
    const { frames, currentFrame, canvas: { size } } = this.props;
    const context = this.canvasRef.current.getContext('2d');
    const frame = frames[currentFrame];
    clearCanvas(context, size);
    if (frame) {
      drawFrame(context, frame, size);
    }
  }

  render() {
    const { canvas: { size }, isOpenFullscreen } = this.props;

    return (
      <div
        className={`preview-fullscreen ${isOpenFullscreen ? 'preview-fullscreen--active' : ''}`}
        ref={this.fullScrennRef}
      >
        <div className="preview-fullscreen__body">
          <canvas className="preview-fullscreen__canvas" width={size} height={size} ref={this.canvasRef} />
          <div className="preview-fullscreen__background" />
        </div>
      </div>
    );
  }
}

const mapStateToProps = ({
  frames, frameRate, currentFrame, canvas, isOpenFullscreen,
}) => (
  {
    frames, frameRate, currentFrame, canvas, isOpenFullscreen,
  }
);

const mapDispatchToProps = { togglePreviewFullscreen };

PreviewFullscreen.propTypes = {
  frames: PropTypes.arrayOf(PropTypes.any).isRequired,
  frameRate: PropTypes.number.isRequired,
  currentFrame: PropTypes.number.isRequired,
  canvas: PropTypes.shape().isRequired,
  isOpenFullscreen: PropTypes.bool.isRequired,
  togglePreviewFullscreen: PropTypes.func.isRequired,
};

export default connect(mapStateToProps, mapDispatchToProps)(PreviewFullscreen);
