import React, { Component } from 'react';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';

import { setPenSize } from '../../redux/actions';

import './PenSizePanel.scss';

class PenSizePanel extends Component {
  constructor() {
    super();
    this.penSizes = [1, 2, 3, 4];
  }

  render() {
    const { penSizes } = this;
    const { penSize, setPenSize: changePenSize } = this.props;
    const penSizeItems = penSizes.map((size, index) => (
      // eslint-disable-next-line react/no-array-index-key
      <li key={index} className="pen-size-panel__item">
        <button
          type="button"
          className={`pen-size-panel__button ${size === penSize ? 'pen-size-panel__button--active' : ''}`}
          onClick={() => { changePenSize(size); }}
        >
          {size}
        </button>
      </li>
    ));

    return (
      <div className="pen-size-panel">
        <ul className="pen-size-panel__list">
          {penSizeItems}
        </ul>
      </div>
    );
  }
}

PenSizePanel.propTypes = {
  penSize: PropTypes.number.isRequired,
  setPenSize: PropTypes.func.isRequired,
};

const mapStateToProps = ({ penSize }) => ({ penSize });

export default connect(mapStateToProps, { setPenSize })(PenSizePanel);
