import React, { Component } from 'react';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';

import { createInitialFrameState } from '../../utils/utils';
import Frame from '../Frame';
import {
  addFrame, setCurrentFrame, deleteFrame, copyFrame, swapFrame,
} from '../../redux/actions';


import './Frames.scss';

class Frames extends Component {
  constructor() {
    super();
    this.handleClick = this.handleClick.bind(this);
    this.handleMouseDown = this.handleMouseDown.bind(this);
  }

  handleClick() {
    const { addFrame: onAddFrame, canvas: { size } } = this.props;
    const frameInitialState = createInitialFrameState(size);
    onAddFrame(frameInitialState);
  }

  handleMouseDown(event) {
    const { swapFrame: swap } = this.props;
    const dragElement = event.target.parentNode;
    dragElement.style.zIndex = '9999';
    const index1 = +dragElement.dataset.index;
    const startY = event.pageY;

    const handleMouseMove = (mouseMoveEvent) => {
      dragElement.style.transform = `translateY(${mouseMoveEvent.pageY - startY}px)`;
    };

    const handleMouseUp = (mouseUpEvent) => {
      dragElement.hidden = true;
      let element = document.elementFromPoint(mouseUpEvent.clientX, mouseUpEvent.clientY);
      dragElement.hidden = false;
      while (element.classList) {
        if (element.classList.contains('frames__drag')) {
          const index2 = +element.dataset.index;
          if (index2 !== index1) {
            swap(index1, index2);
          }
          break;
        }
        element = element.parentNode;
      }
      dragElement.style.transform = 'translateY(0px)';
      dragElement.style.zIndex = '1';
      document.removeEventListener('mousemove', handleMouseMove);
      document.removeEventListener('mouseup', handleMouseUp);
    };

    document.addEventListener('mousemove', handleMouseMove);
    document.addEventListener('mouseup', handleMouseUp);
  }

  render() {
    const {
      frames, canvas: { size }, currentFrame, setCurrentFrame: changeCurrentFrame,
      deleteFrame: onDeleteFrame, copyFrame: onCopyFrame,
    } = this.props;
    const listFrames = frames.map((frame, index) => (
      // eslint-disable-next-line react/no-array-index-key
      <li className="frames__item" key={index}>
        <div
          className="frames__drag"
          onMouseEnter={this.handleMouseEnter}
          onMouseLeave={this.handleMouseLeave}
          data-index={index}
        >
          <Frame
            size={size}
            currentFrame={currentFrame}
            length={frames.length}
            index={index}
            image={frame}
            onDelete={onDeleteFrame}
            onChange={changeCurrentFrame}
            onCopy={onCopyFrame}
          />
          <button
            className="frames__drag-button"
            type="button"
            onMouseDown={this.handleMouseDown}
          >
            {}
          </button>
        </div>
      </li>
    ));

    return (
      <div className="frames">
        <ul className="frames__list">
          {listFrames}
        </ul>
        <button
          type="button"
          className="frames__button"
          onClick={this.handleClick}
        >
          Add new frame
        </button>
      </div>
    );
  }
}

const mapStateToProps = ({ frames, currentFrame, canvas }) => ({ frames, currentFrame, canvas });

const mapDispatchToProps = {
  addFrame,
  setCurrentFrame,
  deleteFrame,
  copyFrame,
  swapFrame,
};

Frames.propTypes = {
  currentFrame: PropTypes.number.isRequired,
  canvas: PropTypes.shape({
    width: PropTypes.number.isRequired,
    height: PropTypes.number.isRequired,
    size: PropTypes.number.isRequired,
    pixelSize: PropTypes.number.isRequired,
  }).isRequired,
  frames: PropTypes.arrayOf(PropTypes.any).isRequired,
  addFrame: PropTypes.func.isRequired,
  setCurrentFrame: PropTypes.func.isRequired,
  deleteFrame: PropTypes.func.isRequired,
  copyFrame: PropTypes.func.isRequired,
  swapFrame: PropTypes.func.isRequired,
};

export default connect(mapStateToProps, mapDispatchToProps)(Frames);
