import { getPixelColor } from '../utils/utils';

const getColor = (context, point, canvasState, changeMainColor) => {
  const { size } = canvasState;
  const frameState = context.getImageData(0, 0, size, size).data;
  const selectedColor = getPixelColor(frameState, point, canvasState.size);
  const isEmptyColor = selectedColor.every((color) => color === 0);
  if (!isEmptyColor) {
    changeMainColor(Array.from(selectedColor));
  }
};

export default getColor;
