import React from 'react';
import {
  BrowserRouter as Router,
  Switch,
  Route,
} from 'react-router-dom';

import Header from '../Header';
import Main from '../Main';
import Landing from '../Landing';

import './App.scss';

const App = () => (
  <Router>
    <div className="App">
      <Header />
      <Switch>
        <Route path="/simple-piskel-clone/create">
          <Main />
        </Route>
        <Route path="/simple-piskel-clone">
          <Landing />
        </Route>
      </Switch>
    </div>
  </Router>
);

export default App;
