import saveFile from 'save-file';
import Gif from 'gif.js-upgrade/dist/gif';

import { clearCanvas, drawFrame } from '../utils/utils';

const getDataUrls = (frames, size) => {
  const canvas = document.createElement('canvas');
  canvas.height = size;
  canvas.width = size;
  const context = canvas.getContext('2d');
  return frames.map((frame) => {
    clearCanvas(context, size);
    drawFrame(context, frame, size);
    return (canvas.toDataURL());
  });
};

const saveAsGif = (
  frames, size, interval = 300, fileName = 'animation',
) => {
  const dataUrls = getDataUrls(frames, size);

  const gif = new Gif({
    workers: 2,
    quality: 10,
    background: '#ffffff',
    width: size,
    height: size,
  });

  dataUrls.forEach((frame) => {
    const image = new Image(size, size);
    image.src = frame;
    gif.addFrame(image, { delay: interval });
  });

  gif.on('finished', (blob) => {
    saveFile(blob, `${fileName}`);
  });

  gif.render();
};

export default saveAsGif;
