import React, { Component } from 'react';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';

import { setMainColor, setSecondColor, swapColors } from '../../redux/actions';
import { rgbaToHex, hexToRgba } from '../../utils/utils';

import './ColorBar.scss';

class ColorBar extends Component {
  constructor() {
    super();
    this.handleChangeMainColor = this.handleChangeMainColor.bind(this);
    this.handleChangeSecondColor = this.handleChangeSecondColor.bind(this);
    this.handleClick = this.handleClick.bind(this);
  }

  handleChangeMainColor(event) {
    const { setMainColor: changeColor } = this.props;
    const element = event.currentTarget;
    changeColor(hexToRgba(element.value));
  }

  handleChangeSecondColor(event) {
    const { setSecondColor: changeColor } = this.props;
    const element = event.currentTarget;
    changeColor(hexToRgba(element.value));
  }

  handleClick() {
    const { swapColors: swap } = this.props;
    swap();
  }

  render() {
    const { mainColor, secondColor } = this.props;
    return (
      <div className="color-bar">
        <input
          className="color-bar__main-color"
          type="color"
          value={rgbaToHex(mainColor)}
          onChange={this.handleChangeMainColor}
        />
        <input
          className="color-bar__second-color"
          type="color"
          value={rgbaToHex(secondColor)}
          onChange={this.handleChangeSecondColor}
        />
        <button
          type="button"
          className="color-bar__button"
          onClick={this.handleClick}
        >
          {}
        </button>
      </div>
    );
  }
}

const mapStateToProps = ({ mainColor, secondColor }) => ({ mainColor, secondColor });

const mapDispatchToProps = {
  setMainColor,
  setSecondColor,
  swapColors,
};

ColorBar.propTypes = {
  setMainColor: PropTypes.func.isRequired,
  setSecondColor: PropTypes.func.isRequired,
  swapColors: PropTypes.func.isRequired,
  mainColor: PropTypes.arrayOf(PropTypes.number).isRequired,
  secondColor: PropTypes.arrayOf(PropTypes.number).isRequired,
};

export default connect(mapStateToProps, mapDispatchToProps)(ColorBar);
