import React, { Component } from 'react';
import PropTypes from 'prop-types';

import { drawFrame } from '../../utils/utils';

import './Frame.scss';

class Frame extends Component {
  constructor() {
    super();
    this.myRef = React.createRef();
  }

  componentDidMount() {
    const { image, size } = this.props;
    if (image) {
      const context = this.myRef.current.getContext('2d');
      drawFrame(context, image, size);
    }
  }

  componentDidUpdate() {
    const { image, size } = this.props;
    if (image) {
      const context = this.myRef.current.getContext('2d');
      drawFrame(context, image, size);
    }
  }

  render() {
    const {
      currentFrame, length, index, size, onDelete, onChange, onCopy,
    } = this.props;
    return (
      <div className={`frame ${currentFrame === index && 'frame--active'}`}>
        <button className="frame__body" type="button" onClick={() => onChange(index)}>
          <canvas className="frame__canvas" id="canvas" width={size} height={size} ref={this.myRef} />
        </button>
        <p className="frame__index">{index + 1}</p>
        {length > 1
          && (
            <button
              type="button"
              className="frame__button frame__button--delete"
              onClick={() => onDelete(index)}
            >
              {}
            </button>
          )}
        <button
          type="button"
          className="frame__button frame__button--copy"
          onClick={() => onCopy(index)}
        >
          {}
        </button>
      </div>
    );
  }
}

Frame.propTypes = {
  currentFrame: PropTypes.number.isRequired,
  size: PropTypes.number.isRequired,
  length: PropTypes.number.isRequired,
  index: PropTypes.number.isRequired,
  image: PropTypes.shape().isRequired,
  onChange: PropTypes.func.isRequired,
  onDelete: PropTypes.func.isRequired,
  onCopy: PropTypes.func.isRequired,
};

export default Frame;
