import React, { Component } from 'react';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';

import { setCurrentTool } from '../../redux/actions';
import Tool from '../Tool';

import './ToolBar.scss';

class ToolBar extends Component {
  constructor() {
    super();
    this.tools = [
      { name: 'pen', key: 'p', label: 'Pen tool' },
      { name: 'line', key: 'l', label: 'Stroke tool' },
      { name: 'fill', key: 'b', label: 'Paint backet tool' },
      { name: 'fill-all', key: 'f', label: 'Paint all pixels' },
      { name: 'eraser', key: 'e', label: 'Erser tool' },
      { name: 'pipette', key: 'c', label: 'Color picker' },
    ];
  }

  clickHandle(toolName) {
    const { setCurrentTool: changeCurrentTool } = this.props;
    changeCurrentTool(toolName);
  }

  render() {
    const { currentTool } = this.props;
    const toolBarItems = this.tools.map((tool, index) => (
      // eslint-disable-next-line react/no-array-index-key
      <li key={index} className="tool-bar__item">
        <Tool
          tool={tool}
          isActive={tool.name === currentTool}
          clickTool={() => { this.clickHandle(tool.name); }}
        />
      </li>
    ));

    return (
      <div className="tool-bar">
        <ul className="tool-bar__list">
          {toolBarItems}
        </ul>
      </div>
    );
  }
}

ToolBar.propTypes = {
  currentTool: PropTypes.string.isRequired,
  setCurrentTool: PropTypes.func.isRequired,
};

const mapStateToProps = ({ currentTool }) => ({ currentTool });

export default connect(mapStateToProps, { setCurrentTool })(ToolBar);
