import React, { Component } from 'react';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';

import { setFrameRate, togglePreviewFullscreen } from '../../redux/actions';
import { createInterval, drawFrame, clearCanvas } from '../../utils/utils';

import './Preview.scss';
import icon from './images/full-screen.svg';


class Preview extends Component {
  constructor() {
    super();
    this.canvasRef = React.createRef();
    this.context = null;
    this.handleChange = this.handleChange.bind(this);
    this.handleClick = this.handleClick.bind(this);
    this.showAnimation = this.showAnimation.bind(this);
    this.previewInterval = createInterval(this.showAnimation);
    this.activeFrame = 0;
  }

  componentDidMount() {
    const { frames, currentFrame, size } = this.props;
    this.context = this.canvasRef.current.getContext('2d');
    drawFrame(this.context, frames[currentFrame], size);
  }

  componentDidUpdate(nextProps) {
    const { frameRate } = this.props;
    const { frameRate: nextFrameRate } = nextProps;
    const MILLISECONDS_PER_SECOND = 1000;
    if (frameRate !== nextFrameRate) {
      this.previewInterval.clear();
      if (frameRate !== 0) {
        const time = MILLISECONDS_PER_SECOND / frameRate;
        this.previewInterval.start(time);
      } else {
        this.drawFrame();
      }
    }
    this.drawFrame();
  }

  showAnimation() {
    const { frames, size } = this.props;
    const { length } = frames;
    const { activeFrame } = this;
    const frame = frames[activeFrame];
    const context = this.canvasRef.current.getContext('2d');
    clearCanvas(context, size);
    if (frame) {
      drawFrame(context, frame, size);
    }
    this.activeFrame = (this.activeFrame + 1 + length) % length;
  }

  drawFrame() {
    const { frames, currentFrame, size } = this.props;
    const context = this.canvasRef.current.getContext('2d');
    const frame = frames[currentFrame];
    clearCanvas(context, size);
    if (frame) {
      drawFrame(context, frame, size);
    }
  }

  handleChange(event) {
    const { setFrameRate: changeFrameRate } = this.props;
    changeFrameRate(+event.target.value);
  }

  handleClick() {
    const { togglePreviewFullscreen: toggleFullscreen } = this.props;
    toggleFullscreen(true);
  }

  render() {
    const { frameRate, size } = this.props;

    return (
      <>
        <div className="preview">
          <canvas className="preview__canvas" width={size} height={size} ref={this.canvasRef} />
          <div className="preview__background" />
          <button className="preview__button" type="button" onClick={this.handleClick}>
            <img className="preview__icon" src={icon} alt="full-screen" />
          </button>
          <div className="preview__control">
            <p className="preview__value">{frameRate}</p>
            <input
              type="range"
              value={frameRate}
              min="0"
              max="24"
              step="1"
              onChange={this.handleChange}
            />
          </div>
        </div>
      </>
    );
  }
}

const mapStateToProps = ({
  frames, frameRate, currentFrame, canvas: { size },
}) => (
  {
    frames, frameRate, currentFrame, size,
  }
);

const mapDispatchToProps = { setFrameRate, togglePreviewFullscreen };

Preview.propTypes = {
  frames: PropTypes.arrayOf(PropTypes.any).isRequired,
  frameRate: PropTypes.number.isRequired,
  currentFrame: PropTypes.number.isRequired,
  size: PropTypes.number.isRequired,
  setFrameRate: PropTypes.func.isRequired,
  togglePreviewFullscreen: PropTypes.func.isRequired,
};

export default connect(mapStateToProps, mapDispatchToProps)(Preview);
