import React, { Component } from 'react';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';

import saveAsGif from '../../services/saveAsGif';
import saveAsApng from '../../services/saveAsApng';
import './ExportMenu.scss';

class ExportMenu extends Component {
  constructor() {
    super();
    this.state = {
      value: 'New Piskel',
    };
    this.handleClickOnGifButton = this.handleClickOnGifButton.bind(this);
    this.handleClickOnApngButton = this.handleClickOnApngButton.bind(this);
    this.handleChange = this.handleChange.bind(this);
  }

  handleClickOnGifButton() {
    const { frames, size, frameRate } = this.props;
    const { value: fileName } = this.state;
    saveAsGif(frames, size, frameRate, fileName);
  }

  handleClickOnApngButton() {
    const { frames, size, frameRate } = this.props;
    const { value: fileName } = this.state;
    saveAsApng(frames, size, frameRate, fileName);
  }

  handleChange(event) {
    this.setState({ value: event.target.value });
  }

  render() {
    const { value } = this.state;
    return (
      <div className="export-menu">
        <p className="export-menu__title">Export as:</p>
        <input className="export-menu__input" type="text" value={value} onChange={this.handleChange} />
        <div className="export-menu__button-group">
          <button
            className="export-menu__button"
            type="button"
            onClick={this.handleClickOnGifButton}
          >
            gif
          </button>
          <button
            className="export-menu__button"
            type="button"
            onClick={this.handleClickOnApngButton}
          >
            apng
          </button>
        </div>
      </div>
    );
  }
}

const mapStateToProps = ({ frames, frameRate, canvas: { size } }) => ({ frames, frameRate, size });

ExportMenu.propTypes = {
  frames: PropTypes.arrayOf(PropTypes.any).isRequired,
  frameRate: PropTypes.number.isRequired,
  size: PropTypes.number.isRequired,
};

export default connect(mapStateToProps)(ExportMenu);
